/*
 * SignIn css File
*/
.SignIn {
    text-align: left;
}

.sign-in-up-form {
	margin: 10% auto 0 auto;
	padding: 30px;
	width: 400px;
	height: auto;
	overflow: hidden;
	background: rgba(47, 46, 46, 0.7);
	border-radius:10px;
	box-shadow:  2px 2px 10px 10px rgba(0, 0, 0, 0.4), 
				-2px -2px 10px 10px rgba(0, 0, 0, 0.4);
}
.sign-in-up-form input {
	float:left;
	clear:both;
}

.sign-in-up-form input {
	margin:15px 0;
	padding:15px 10px;
	width:100%;
	outline:none;
	border:1px solid #bbb;
	border-radius:20px;
	display:inline-block;
	box-sizing:border-box;
	transition:0.2s ease all;
}

.sign-in-up-form input[type=text]:focus,
.sign-in-up-form input[type="password"]:focus {
	border:1px solid cornflowerblue;
}

input[type=submit] {
	padding:15px 50px;
	width:100%;
	background:brown;
	border:none;
	color:white;
	cursor:pointer;
	display:inline-block;
	clear: both;
	transition:0.2s ease all;
}

input[type=submit]:hover {
	background-color: rgb(124, 31, 31);
}

input[type="submit"]:active {
	background-color: rgb(124, 31, 31);
}

input[type="submit"]:disabled {
	background-color: rgb(124, 31, 31);
}

.form-item {
	display: flow-root;
}

.form-item-header {
	display: flex;
	position: relative;
}

.invalid-message {
	color: red;
	font-size: 12px;
	position: absolute;
	right: 0;
	padding: 5px 0;
}

.font-white {
	color: white;
}
::-webkit-input-placeholder {
	color: rgb(40,40,40);
